import React, { Component } from "react"
import { graphql, Link } from "gatsby"
import styled, { keyframes } from "styled-components"
import posed from "react-pose"
import nav_up from "../images/nav_up.svg"
import nav_close from "../images/nav_close.svg"
import nav_down from "../images/nav_down.svg"
import { ReactComponent as ChainIcon } from "../images/chain.svg"
import { DOMParser } from "xmldom"
import {
  PostHeaderTitle,
  Header,
  HeaderIcon,
  SideBarLink,
  RightSidebar,
  SideBarTitle,
  IndentedContent,
  SidebarLinkWithIcon,
  HeaderNumber, HeaderTitle, CenteredParagraph,
} from "../components/style"
import pointer from "../images/pointer.svg"
import { ReactComponent as PostIcon } from "../images/chain.svg"
import { BrowserView, MobileView } from "react-device-detect"
import burgerMenu from "../images/hamburger_menu.svg"
import SEO from "../components/seo"
import AboutModal, { BioImage } from "../pages/aboutme"


const SideBar = styled.div`
  display: inline-block;
  vertical-align: top;
  text-align: left;
  background-color: #fefefe;
  position: sticky;
  top: 65px;
    overflow: hidden;
`

const SideBarContent = styled.div`
  padding: 5px 0;  
  ul {
    list-style: none;
    text-align: left;
    margin: 0 auto;
    padding-left: 3px;
  }
  li {
    a {
      border: 0;
    }
  }
`

const ContentLink = styled.div`
  width: 178px;
  height: 25px;
  text-decoration: none;
  display: inline-block;
`

const TableOfContent = styled.div`
  width: 218px;
  display: inline-block;
  border: 1px solid rgba(104,115,219,0.56);
`

const SideBarAnimator = posed(SideBar)({
  enter: {
    x: 0,
    opacity: 1,
    transition: { ease: "easeOut", duration: 600 },
  },
  exit: {
    x: -600,
    opacity: 0,
    transition: { ease: "easeOut", duration: 600 },
  },
})

const SideBarWrapper = styled.div`
  width: 250px;
  margin-top: 10px;
  flex-wrap: wrap;
  display: inline-flex;
`

const PostContainer = styled.div`
  width: 745px;
  display: inline-block;
  text-align: left;
  @media only screen and (min-width: 1400px) {
    width: 1000px;
  }
  
  @media only screen and (max-width: 768px) {
    width: 100vw;
  }
`

const RightToLeft = posed(PostContainer)({
  enter: {
    x: 0,
    opacity: 1,
    transition: { ease: "easeOut", duration: 600 },
  },
  exit: {
    x: 400,
    opacity: 0,
    transition: { ease: "easeOut", duration: 600 },
  },
})

const Anchor = styled.div`
  width:0;
  height:0;
`

const TopAnchor = styled(Anchor)`
  position: absolute;
  top: -30px;
`

const PostFrame = styled.div`
  position: relative;
  min-height: 300px;
`

const PostContent = styled.div`
  margin: 15px;
  display: inline-block;
  p {
   padding: 0 10px;
  }
`

const CommentoWrapper = styled.div`
  margin: 15px;
`

const Navigator = styled.div`
  position: sticky;
  top: 90px;
  display: inline-block;
  vertical-align: top;
  margin-left: 15px;
  
  @media only screen and (max-width: 768px) {
    width: 100%;
    height: 44px;
    z-index: 5;
    bottom: 0;
    left: -15px;
    top: auto;
    position: fixed;
    text-align: center;
    background-color: #f1f1f1;
    border-top: 1px solid #bebebe;
    padding-top: 6px;
  }
`

const Nav = styled(Link)`
  background-color: #3c3c3c;
  width: 50px;
  height: 50px;
  margin: 0;
  color: #f1f1f1;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  display: block;
    -webkit-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  border: 1px #999999 solid;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    -moz-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -2px) scale(1.01);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.2s ease-out;
    }
  }
  @media only screen and (max-width: 768px) {
    width: 30px;
    height: 30px;
    display: inline-block;
  }
`

const NavUp = styled(Nav)`
  background-image: url(${nav_up});
  background-color: #748cc5;
`

const NavClose = styled(Nav)`
  background-image: url(${nav_close});
  background-size: 70%;
  background-color: #cb586a;
`

const NavSpacer = styled.div`
  height: 10px;
  @media only screen and (max-width: 768px) {
    display: inline-block;
    width: 10px;
  }
`

const NavDown = styled(Nav)`
  background-image: url(${nav_down});
  background-color: #748cc5;
`
const Post = styled.div`
  display: inline-block;
  width: 650px;
  @media only screen and (min-width: 1400px) {
    width: 900px;
  }
  
    @media only screen and (max-width: 768px) {
   width: 100vw;
  }
`

