class Commento extends React.Component {
  constructor(...args) {
    super(...args)
    this.ref = React.createRef()
  }

  render() {
    return (
      <CommentoWrapper>
        <div id="commento"></div>
        <div ref={this.ref}/>
      </CommentoWrapper>
    )
  }

  componentDidMount() {
    var async = true
    const s = document.createElement("script")
    s.async = async
    s.src = "https://cdn.commento.io/js/commento.js"
    // s.setAttribute('data-timestamp', +new Date())
    this.ref.current.appendChild(s)
  }
}

const PostWrapper = styled.div`
  width: 1000px;
  margin: 0 auto;
  @media only screen and (min-width: 1400px) {
      width: 1280px;
  }
  
    @media only screen and (max-width: 768px) {
    width: 100vw;
  }
`

const ContentName = styled.span`
  vertical-align: middle;
`

const ContentNumber = styled.span`
  line-height: 28px;
  &:after{
    content:". ";
  }
`
const unHide = keyframes`
  0% {
    transform: skewX(-45deg) translateX(-25%);
  }
  100% {
    transform: skewX(-45deg) translateX(100%);
  }
`

const Mask = styled.div`
  background-color:  #fefefe;
  width: 200%;
  height: 100%;
  transform: skewX(-45deg) translateX(-25%);
  position: absolute;
  top: 0;
  left: 0;
  animation: ${unHide} 1.5s ease-out 0s forwards;
`
const PullTab = styled.div`
  width: 35px;
  height: 35px;

  position: fixed;
  left: 10px;
  top: 55px;
  z-index: 4;
  background-image: url(${burgerMenu});
  background-size: cover;
  
  background-color: #fefefe;
  background-image: url(${burgerMenu});
`

class BlogPost extends Component {
  // constructor to set state and bind "this"
  state = {
    isExpanded: false
  };

  constructor(props) {
    super(props);
  }

  // function to handle the click
  expandClick = () => {
    this.setState(state => ({ isExpanded: !state.isExpanded }));
  };

  foldClick = () => {
    this.setState(state => ({ isExpanded: false }));
  };

  render() {

    const post = this.props.data.markdownRemark;
    console.log(this.props)
    // the return() to put your default HTML into the DOM
    return (
      <PostWrapper>
        <PostFrame>
          <TopAnchor id="top-anchor"/>
          <RightSidebar active={this.state.isExpanded?1:0}>
            <SideBarWrapper>
              <SideBarTitle>
                TABLE OF CONTENT
              </SideBarTitle>
              <IndentedContent>
                {(() => {
                  if (post.tableOfContents !== undefined && post.tableOfContents !== "") {
                    const toc = new DOMParser().parseFromString(post.tableOfContents, "text/html")
                    if (toc !== undefined) {
                      const list = toc.getElementsByTagName("a")
                      let allData = []
                      let iterator = 0
                      for (let listData of Array.from(list)) {
                        let element = Array.from(listData.childNodes).map(el => el.nodeValue)
                        if (element.length > 0) {
                          iterator++
                          let currentLi = React.createElement("li", { "key": "li_" + element[0] }, [
                              React.createElement(SidebarLinkWithIcon, {
                                  "key": "lwi_" + iterator,
                                  "to": listData.getAttribute("href"),
                                  "active": 0,
                                },
                                React.createElement(SideBarLink, {}, [
                                  React.createElement(ChainIcon, { "key": "ci_" + iterator }),
                                  React.createElement(ContentNumber, { "key": "cnum_" + iterator }, iterator.toString().padStart(2, "0")),
                                  React.createElement(ContentName, { "key": "cnam_" + iterator }, element[0]),
                                ]),
                              ),
                            ],
                          )
                          allData.push(currentLi)
                        }
                      }
                      return React.createElement(SideBarContent, {},
                        React.createElement("ul", {}, allData),
                      )
                    }
                  }
                })()}
              </IndentedContent>
            </SideBarWrapper>

          </RightSidebar>
          <MobileView>
            <PullTab onClick={this.expandClick}  active={this.state.isExpanded?1:0} />
          </MobileView>
          <RightToLeft>
            <Post>
              <Header>
                <HeaderIcon>
                  <PostIcon/>
                </HeaderIcon>
                <PostHeaderTitle>
                  <h1>{post.frontmatter.title}</h1>
                </PostHeaderTitle>
              </Header>
              <PostContent dangerouslySetInnerHTML={{ __html: post.html }}/>
              <Commento/>
            </Post>
            <Navigator>
              <NavUp to={post.fields.path + "/#top-anchor"}/>
              <NavSpacer/>
              <NavClose to={"/#"} onClick={(event) => {
                window.history.back()
                event.preventDefault()
              }}/>
              <NavSpacer/>
              <NavDown to={post.fields.path + "/#bottom-anchor"}/>
            </Navigator>
          </RightToLeft>
          <Anchor id="bottom-anchor"/>
        </PostFrame>
      </PostWrapper>
    );
  }
}


export default BlogPost;

